<template>
  <b-container>
    <p>
      Find a mailing address for forms and documents by entering the form
      number/name and the location of your home, business or property.
    </p>
    <div class="text-left">
      <b-form @submit="onSubmit" @reset="onReset" v-if="show" novalidate>
        <b-form-group
          id="exampleInputGroup1"
          label="Form number:"
          label-for="exampleInput1"
          description="Enter the form number"
        >
          <b-form-input
            id="exampleInput1"
            type="text"
            autocomplete="off"
            v-model="form.formnumber"
            v-on:change="changeform"
            required
            placeholder="Enter Form Number"
          />
        </b-form-group>

        <b-form-group
          id="exampleInputGroup3"
          label="Location of your home, business or property:"
          label-for="exampleInput3"
        >
          <b-form-select
            id="exampleInput3"
            :options="regions"
            plain
            v-model="form.region"
            v-on:change="changeregion"
          />
        </b-form-group>

        <b-button type="submit" variant="primary">Find</b-button>
        <!-- <b-button type="reset" variant="danger">Reset</b-button> -->
      </b-form>
    </div>
    <b-badge class="pt-3" variant="light">Mail to:</b-badge>
    <div class="lead text-left d-flex pl-2">
      <p class="h4">{{ this.resultaddress.line1 }}</p>
      <p class="h4">{{ this.resultaddress.line2 }}</p>
      <p class="h4">{{ this.resultaddress.line3 }}</p>
    </div>
    <!-- following is for debug use -->
    <div class="d-flex text-left mt-4">
      <hr/>
      <p class="mt-5">
        Form: {{ this.form.formtolookup }} - Treatment:
        {{ this.form.formtreatment }}
      </p>
      <!-- <p class="mt-5">Address:{{ this.resultaddress }}</p>
      <p class="mt-5">{{ this.addresses }}</p>
      <p>{{ this.treatments }}</p>
      <p>{{ this.formtotemplate }}</p> -->
    </div>
  </b-container>
</template>

<script>
import axios from "axios";
export default {
  computed: {
    selectedregion: function() {
      return this.form.region === null ? null : this.form.region.slice(0, -1);
    },
    numbervalidation() {
      return this.form.formtolookup.length > 0;
    }
  },
  mounted() {
    axios
      .get("data/addresscodes.json")
      .then(response => (this.addresses = response.data));
    axios
      .get("data/treatments.json")
      .then(response => (this.treatments = response.data));
    axios
      .get("data/forms.json")
      .then(response => (this.formtotemplate = response.data));
  },
  data() {
    return {
      form: {
        formnumber: "",
        formtolookup: "",
        formtreatment: null,
        name: "",
        region: null,
        regiontolookup: null,
        checked: []
      },
      defaultaddress: "JON-TC",
      formnum: "",
      resultaddress: {
        line1: "",
        line2: "",
        line3: "",
        fax: ""
      },
      formtotemplate: {
        T1: {
          tmt: "tax-benefits"
        }
      },
      treatments: {
        "tax-benefits": {
          FIXED: "",
          ONTARIO: "SUD-TC",
          ATLANTIC: "JON-TC",
          QUEBEC: "JON-TC",
          PRAIRIE: "WPG-PO-14001",
          PACIFIC: "WPG-PO-14001",
          OTHER: "SUD-TC"
        }
      },
      addresses: {
        "JON-TC": {
          line1: "Jonquière Tax Centre",
          line2: "2251 René-Lévesque Boulevard",
          line3: "Jonquière QC G7S 5J2",
          fax: ""
        },
        KelownaDrop: {
          line1: "Kelowna Tax Services office",
          line2: "200 - 471 Queensway Avenue",
          line3: "Kelowna BC V1Y 6S5",
          fax: "250-492-8346"
        }
      },
      regions: [
        { text: "Select One", value: null },
        { text: "Alberta", value: "PRAIRIE1" },
        { text: "British Columbia", value: "PACIFIC1" },
        { text: "Manitoba", value: "PRAIRIE2" },
        { text: "New Brunswick", value: "ATLANTIC1" },
        { text: "Newfoundland and Labrador", value: "ATLANTIC4" },
        { text: "Northwest Territories", value: "PRAIRIE4" },
        { text: "Nova Scotia", value: "ATLANTIC2" },
        { text: "Nunavut", value: "ONTARIO2" },
        { text: "Ontario", value: "ONTARIO1" },
        { text: "Prince Edward Island", value: "ATLANTIC3" },
        { text: "Quebec", value: "QUEBEC1" },
        { text: "Saskatchewan", value: "PRAIRIE3" },
        { text: "Yukon", value: "PACIFIC2" },
        {
          text: "Outside Canada",
          value: "OTHER1"
        }
      ],
      show: true
    };
  },
  methods: {
    changeform(value) {
      this.form.formtolookup = value
        .trim()
        .replace(/\s/g, "")
        .toUpperCase();
      if (typeof this.formtotemplate[this.form.formtolookup] === "undefined") {
        this.form.formtreatment = null;
      } else {
        this.form.formtreatment = this.formtotemplate[
          this.form.formtolookup
        ].tmt;
        if (this.form.formtreatment === "") {
          this.form.formtreatment = null;
        }
      }
      this.findtheaddress();
      // alert(JSON.stringify(value + this.form.formtolookup + this.form.formtreatment));
    },
    changeregion(value) {
      this.form.regiontolookup = value === null ? null : value.slice(0, -1);
      this.findtheaddress();
      // alert(JSON.stringify(value + this.form.regiontolookup));
    },
    findtheaddress() {
      if (this.form.formtreatment !== null) {
        var tmt = this.treatments[this.form.formtreatment];
        if (typeof tmt !== "undefined") {
          var loc = tmt["FIXED"];
          if (loc == "") {
            if (this.form.regiontolookup != null) {
              loc = tmt[this.form.regiontolookup];
            }
          }
          // alert(JSON.stringify(tmt) + JSON.stringify(loc));

          if (loc != "") {
            var addressfound = this.addresses[loc];
            // alert(JSON.stringify(this.form) + JSON.stringify(addressfound));

            if (typeof addressfound !== "undefined") {
              this.resultaddress = addressfound;
              this.formnum = this.form.formtolookup;
              return true;
            }
          }
        }
      }
      this.clearaddress();
      return false;
    },
    clearaddress() {
      this.resultaddress = {
        line1: "",
        line2: "",
        line3: "",
        fax: ""
      };
      this.formnum = "";
    },
    onSubmit(evt) {
      evt.preventDefault();

      this.findtheaddress();
    },
    onReset(evt) {
      evt.preventDefault();
      /* Reset our form values */
      this.form.formnumber = "";
      this.form.name = "";
      this.form.region = null;
      this.form.checked = [];
      /* Trick to reset/clear native browser form validation state */
      this.show = false;
      this.$nextTick(() => {
        this.show = true;
      });
    }
  }
};
</script>
